import React from 'react'
import { ReactElement } from 'react'

import { SubjectSummary } from '../../../components/subject/SubjectSummary'
import { SubjectContent } from '../../../components/subject/SubjectContent'
import { SubjectTitle } from '../../../components/subject/SubjectTitle'
import { SubjectKeyword } from '../../../components/subject/SubjectKeyword'
import { SubjectIdentifier } from '../../../components/subject/SubjectIdentifier'
import { Subject } from '../../../components/subject/Subject'

export function Evocation () : ReactElement {
  return (
    <Subject>
      <SubjectIdentifier>magic-spells-evocation</SubjectIdentifier>
      <SubjectTitle>Évocation</SubjectTitle>
      <SubjectSummary>
      </SubjectSummary>
      <SubjectContent>
        <p>
          <strong>L'évocation d'un sortilège</strong> peut être subdivisé en
          autant d'action que nécéssaire appellées <strong>action
          d'évocation</strong>. Lors d'une telle action, le magicien doit
          réussir un test de maîtrise de l'évocation en opposition avec un degré
          de difficulté calculé à l'avance pour pouvoir avancer ou finaliser son
          sortilège. Si le test d'évocation est un succès le magicien avance un
          peut plus dans l'exécution de son sort, si le test échoue, on parle
          alors d'échec de l'évocation, ou de
          <strong>catastrophe ésothérique</strong>.
        </p>

        <p>
          Le <strong>degré de difficulté</strong> d'une action d'incantation
          dépend du nombre de symboles à exécuter en une même action. Un
          magicien est libre de choisir ce nombre de symbole au commencement de
          son action. Pour chaque symbole à incanter, la difficulté de l'action
          augmente de 3 points. L'usage de certains atouts peut réduire ce degré
          de difficulté de base.
        </p>

        <p>
          Certains <strong>malus contextuels</strong> peuvent s'appliquer dans
          le cadre d'un test d'évocation :
        </p>

        <ul>
          <li>
            Pour chaque blessure légère non-traité, le magicien voit augmenter
            la difficulté de son incantation d'un point.
          </li>
          <li>
            Pour chaque blessure grave non-traité du magicien, celui-ci se
            voit attribuer un malus de 4 points à toute ses tentatives
            d'incantation.
          </li>
        </ul>
      </SubjectContent>
    </Subject>
  )
}
